import React from 'react'

const Contact = (props) => (
    <section id="contact">
        <div className="inner">
            
            <section>
                <section>
                    <div className="contact-method">
                        <span className="icon alt fa-envelope"></span>
                        <h3>Email</h3>
                        <a href="mailto:mweisert@gmail.com">mweisert@gmail.com</a>
                    </div>
                </section>
                <section>
                    <div className="contact-method">
                        <span className="icon alt fa-phone"></span>
                        <h3>Phone</h3>
                        <span>312.857.4499</span>
                    </div>
                </section>
            </section>
        </div>
    </section>
)

export default Contact



