import React from 'react'
import Link from 'gatsby-link'

const Header = (props) => (
    <header id="header" className="alt">
        <Link to="/" className="logo"><span>Mike Weisert</span><span> UX Designer</span></Link>
        <nav>
        <a href="https://docs.google.com/document/d/1ZhrqCdPchfWBGle1xf4eP5e0IdhJwJ7ucKjC78AdlU4/edit?usp=sharing" target="_blank"> Resume <span className="icon fa-download"></span></a>
            <a className="menu-link" onClick={props.onToggleMenu} href="javascript:;">Menu</a>
        </nav>
    </header>
)

Header.propTypes = {
    onToggleMenu: React.PropTypes.func
}

export default Header



